.container {
  box-sizing: border-box;
  grid-template-rows: repeat(2, 1fr);
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  align-items: center;
  gap: 46px 48px;
  margin: 0 auto;
  padding: 50px 40px;
  font-family: Roboto, Arial, sans-serif;
  display: grid;
}

@media (width >= 488px) {
  .container {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .container {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .container {
    grid-template-columns: repeat(4, 200px);
  }
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  font-family: Roboto, Arial, Helvetica, sans-serif;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo {
  padding-right: 243px;
  display: flex;
}

@media (width >= 1200px) {
  .header__logo {
    padding-right: 419px;
  }
}

.nav {
  color: #060b35;
}

.nav__list {
  margin: 0;
  padding: 0;
  font-size: 12px;
  list-style: none;
  display: flex;
}

.nav__item {
  text-transform: uppercase;
  align-items: center;
  display: flex;
}

.nav__item:not(:last-child) {
  margin-right: 20px;
}

.nav__link {
  color: inherit;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  transition: color .3s;
}

.nav__link:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.card {
  color: #060b35;
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 16px;
  transition: all .3s;
  display: flex;
}

.card__image {
  width: 160px;
  height: 134px;
  margin: 16px 3px 40px;
}

.card__description {
  flex-direction: column;
  gap: 4px;
  margin-bottom: 16px;
  display: flex;
}

.card__description-name {
  font-size: 12px;
  font-weight: 500;
  line-height: 150%;
}

.card__description-code {
  color: #616070;
  font-size: 10px;
  line-height: 140%;
}

.card__rate {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  display: flex;
}

.card__rate-reviews {
  font-size: 10px;
  line-height: 140%;
}

.card__price {
  justify-content: space-between;
  margin-bottom: 16px;
  display: flex;
}

.card__price-text {
  color: #616070;
  font-size: 12px;
  line-height: 150%;
}

.card__price-value {
  font-size: 16px;
  font-weight: 700;
  line-height: 112.5%;
}

.card__button {
  box-sizing: border-box;
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #0000;
  border-radius: 5px;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 40px;
  text-decoration: none;
  transition: all .3s;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__description-name {
  color: #34568b;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:not(:last-child) {
  margin-right: 4px;
}

.stars--1 :nth-child(-n+1), .stars--2 :nth-child(-n+2), .stars--3 :nth-child(-n+3), .stars--4 :nth-child(-n+4), .stars--5 :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}
